<template>
  <div class="personal-nav" id="personalNav">
    <div class="head">
      课程大纲
      <span v-if="founded" class="create-btn iconfont icon-tianjiahuabu" @click="addProgram"></span>
    </div>
    <div class="content" v-loading="updata">
      <div v-show="!founded" class="not-wrap">
        <div class="img-box">
          <img src="../../../assets/not_outline.png" :draggable="false">
        </div>
        <el-button class="create-btn" type="primary" @click="addProgram" size="small">创建大纲</el-button>
      </div>
      <el-tree
        v-if="!updata"
        v-show="founded"
        ref="tree"
        lazy
        accordion
        node-key="id"
        :load="loadNode"
        :props="props"
        @node-contextmenu="rihgtClick"
        @node-click="selectNode"
      ></el-tree>
      <div v-show="showProgramMenu" class="text-menu" id="ProgramMenu">
        <div @click="addContent">添加目录</div>
        <div @click="editProgram">编辑大纲</div>
        <div @click="del('Program')">删除大纲</div>
      </div>
      <div v-show="showContentMenu" class="text-menu" id="ContentMenu">
        <div @click="addContent">添加目录</div>
        <div @click="editContent">编辑目录</div>
        <div @click="del('Context')">删除目录</div>
        <div>立即备课</div>
      </div>
    </div>
    <!-- 确认删除提示 -->
    <el-dialog
      class="delete-dialog"
      title="智慧校园提示您"
      :close-on-click-modal="false"
      :visible.sync="confirmDeleteDialog"
      center>
      <span>确定要彻底删除吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="confirmDeleteDialog = false">取 消</el-button>
        <el-button type="primary" @click="confirmDeleteHandler">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import http from '../../../utils/http'
import api from '../../../utils/api'
import { setAttrById } from '../../../utils/dom.js'
export default {
  props: ['founded'],
  data () {
    return {
      updata: false,
      tpid: '',
      tpname: '',
      tcid: '',
      props: { label: 'name', isLeaf: 'leaf' },
      showProgramMenu: false,
      showContentMenu: false,
      confirmDeleteDialog: false,
      deltype: ''
    }
  },
  mounted () {
    document.addEventListener('click', e => {
      if (this.showProgramMenu) this.showProgramMenu = false
      if (this.showContentMenu) this.showContentMenu = false
    })
  },
  methods: {
    //加载节点
    async loadNode (node, resolve) {
      let treeData = []
      if (node.level == 0) {
        const res = await http.get(api.getTeachingProgramNameList, {
          token: http.getToken()
        })

        if(res.Datas != null && res.Datas != undefined) {
          res.Datas.forEach(element => {
            treeData.push({
              id: element.TPID,
              name: element.TPName
            })
          })
        }
        return resolve(treeData)
      } else if (node.level == 1) {
        this.tpid = node.data.id
        this.tpname = node.data.name
        const res = await http.get(api.getTeachingContentNameList, {
          tpid: this.tpid,
          ptcid: null,
          token: http.getToken()
        })
        res.Datas.forEach(element => {
          treeData.push({
            id: element.TCID,
            name: element.TCTitle,
            tpid: this.tpid,
            tpname: this.tpname
          })
        })
        setTimeout(() => {
          resolve(treeData)
        }, 100)
      } else {
        const res = await http.get(api.getTeachingContentNameList, {
          tpid: this.tpid,
          ptcid: node.data.id,
          token: http.getToken()
        })
        res.Datas.forEach(element => {
          treeData.push({
            id: element.TCID,
            name: element.TCTitle,
            tpid: this.tpid,
            tpname: this.tpname
          })
        })
        setTimeout(() => {
          resolve(treeData)
        }, 100)
      }
    },
    addProgram () {
      this.$router.push('createProgram')
    },
    addContent () {
      this.$router.push({
        name: 'createContent',
        params: {
          tpid: this.tpid,
          tpname: this.tpname,
          tcid: this.tcid
        }
      })
    },
    editProgram () {
      this.$router.push({ name: 'createProgram', query: { tpid: this.tpid } })
    },
    editContent () {
      this.$router.push({ name: 'createContent', query: { tcid: this.tcid } })
    },
    rihgtClick: function (ev, obj, val, ele) {
      this.showProgramMenu = false
      this.showContentMenu = false
      if (val.level == 1) {
        this.tpid = val.data.id
        this.tpname = val.data.name
        this.showProgramMenu = true
        if (this.showProgramMenu) {
          setAttrById('ProgramMenu', 'style', `top:${ev.pageY}px;left:${ev.pageX}px`)
        }
      } else {
        this.tcid = val.data.id
        this.showContentMenu = true
        if (this.showContentMenu) {
          setAttrById('ContentMenu', 'style', `top:${ev.pageY}px;left:${ev.pageX}px`)
        }
      }
    },
    del (type) {
      this.deltype = type
      this.confirmDeleteDialog = true
    },
    confirmDeleteHandler: async function () {
      this.confirmDeleteDialog = false
      if (this.deltype == 'Program') {
        const res = await http.post(api.deleteByTPID, {
          tpid: this.tpid,
          Token: http.getToken()
        })
        if (res.type) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          })
        } else {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
          this.$refs.tree.remove(this.tpid)
        }
      } else {
        const res = await http.post(api.deleteByTCID, {
          tcid: this.tcid,
          Token: http.getToken()
        })
        if (res.type) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          })
        } else {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
          this.$refs.tree.remove(this.tcid)
        }
      }
    },
    selectNode: function (obj, val, ele) {
      this.showProgramMenu = false
      this.showContentMenu = false
      if (val.level == 1) {
        this.tpid = val.data.id
        this.tpname = val.data.name
        this.$router.push({ name: 'programView', query: { tpid: this.tpid } })
      } else {
        this.tcid = val.data.id
        this.$router.push({ name: 'contentView', query: { tcid: this.tcid } })
      }
    }
  },
  watch: {
    updata (newVal, oldVal) {
      if (newVal) {
        setTimeout(() => {
          this.updata = false
        }, 152)
      }
    }
  }
}
</script>
<style lang="less">
.personal-nav {
  width: 245px;
  padding-bottom: 66px;
  background: #fff;
  float: left;
  text-align: left;
  user-select: none;
  margin-bottom: 25px;
  .head {
    width: 100%;
    height: 40px;
    line-height: 40px;
    background: #3a97ff;
    color: #fff;
    padding-left: 22px;
    .create-btn {
      float: right;
      font-size: 1.1rem !important;
      margin-right: 8px;
      &:hover {
        cursor: pointer;
      }
    }
  }
  .content {
    padding-top: 18px;
    .nav-title {
      font-size: 1rem !important;
      .iconfont {
        font-size: 1.2rem !important;
        color: #ffca28;
      }
    }
    .not-wrap {
      width: 100%;
      margin-top: 25px;
      text-align: center;
      .img-box {
        width: 80%;
        margin: 0 auto;
        img {
          width: 100%;
        }
      }
      .create-btn {
        margin-top: 25px;
        &:hover {
          cursor: pointer;
        }
      }
    }
    .text-menu {
      width: 125px;
      position: absolute;
      border-radius: 6px;
      background: #fff;
      box-shadow: 0 0 2px #aaa;
      text-align: center;
      padding: 12px 0;
      div {
        width: 100%;
        padding: 8px 0;
        color: #bbb;
        &:hover {
          cursor: pointer;
          color: #000;
          border-left: 2px solid #3a97ff;
          background: #edf6ff;
        }
      }
    }
  }
  .el-tree {
    .el-tree-node {
      color: #bbb;
      box-sizing: border-box;
      padding: 8px 0;
      .el-tree-node__content {
        .el-tree-node__label {
          font-size: 1rem !important;
        }
        &:hover {
          background: #edf6ff !important;
        }
      }
    }
    .is-current {
      border-left: 2px solid #3a97ff !important;
      background: #edf6ff !important;
      color: #000 !important;
    }
  }
  .el-menu-item.is-active {
    border-left: 2px solid #3a97ff !important;
    background: #edf6ff !important;
    color: #000 !important;
  }
  .delete-dialog {
    .el-dialog {
      width: 400px !important;
    }
  }
}
</style>



