<template>
  <div style="width:100%">
    <out-line-side-nav :founded="isHas" ref="nav"></out-line-side-nav>
    <div class="outline-detail">
      <router-view :founded="isHas" :tpidFirst="tpid"></router-view>
    </div>
  </div>
</template>
<script>
import http from "../../../../utils/http";
import api from "../../../../utils/api";
import outLineSideNav from "../../../../components/personal/teacher/outlineSideNav.vue";

export default {
  components: {
    outLineSideNav
  },
  data() {
    return {
      isHas: false,
      tpid: ""
    };
  },
  created() {
    this.initData();
  },
  methods: {
    initData: async function() {
      let barList = [];
      const res = await http.get(api.getTeachingProgramNameList, {
        Token: http.getToken()
      });
      if (res.Result) {
        res.Datas.forEach((element, index) => {
          if (index == 0) this.tpid = element.TPID;
          barList.push({
            value: element.TPID,
            label: element.TPName
          });
        });
      }

      if (barList.length <= 0) {
        this.isHas = false;
      } else {
        this.isHas = true;
      }
    }
  }
};
</script>
<style lang="less">
.outline-detail {
  width: 925px;
  float: right;
  .outline-wrap {
    width: 100%;
    padding: 15px;
    background: #fff;
    text-align: left;
    padding-top: 0;
    .head {
      padding: 15px 0;
    }
    .create-form {
      width: 100%;
      padding: 15px;
      background: #f8f8f8;
      .btns {
        width: calc(100% + 80px);
        text-align: center;
        margin-left: -80px;
      }
    }
  }
}
</style>



